@import url(https://fonts.googleapis.com/css?family=Roboto);

body {
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

html,
body {
  box-sizing: border-box;
}
body * {
  box-sizing: inherit;
}
.banner {
  position: relative;
  width: 100%;
}
.banner-editorial {
  position: absolute;
  top: 20px;
  width: 100%;
  padding: 0 20px;
  text-align: left;
  font: Roboto;
  color: white;
  font-family: Roboto;
}
.banner-header {
  font-size: 30px;
}
.banner-background-image {
  width: 100%;
  height: auto;
}
.banner-call-to-action {
  position: absolute;
  bottom: 20px;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  right: 20px;
  padding: 10px;
  text-decoration: none;
  font-family: Roboto;
}
.banner-call-to-action:hover {
  background-color: rgba(0, 0, 0, 1);
}
.banner-call-to-action:after {
  content: " >";
}
